/**
 * Language list
 */

 export default class Language{
    public static LANG : {[key:string] : string} = {};
    public static init(){
        cc.loader.loadRes("data/TLanguage",(err, json)=>{
            if (err){
                console.log("List read error,"+err);
                return ;
            }
            console.log(json);
            for(let detail of json){
                this.LANG[detail.key] = detail.data;
            }
            console.log(this.LANG);
        })
    }

    public static getLang(key : string){
        let str = key;
        if (this.LANG[key]){
            str = this.LANG[key];
        }
        return str;
    }
 }